
.register {
  position: relative;
  width: 100%;
  height: 100%;

  .form_container {
    width: 420px;
    height: 210px;
    position: absolute;

    padding: 25px;
    border-radius: 5px;
    text-align: center;
    left: 48%;
    transform: translateX(-50%);
    top: 30%;
    opacity: 0.8;

    .manage_tip {
      .title {
        text-align: center;
      }
    }

    .el-form {
      margin-top: 20px;
      padding: 35px 35px 20px 4px;
      border-radius: 5px;
      box-shadow: 0px 5px 10px #cccc;

      ::v-deep .el-input__inner {
        width: 95%;
      }
      :deep(.el-form-item__label) {
        line-height: 24px;
        font-size: 14px;
        color: black;
        padding-top: 5px;
      }

      :deep(.el-input__wrapper) {
        background-color: rgb(94, 90, 90);
        .el-input__inner {
          color: #fff;
          padding: 20px 0;
        }
      }

      .el-button {
        width: 100%;
        margin-left: -50px;
      }
    }
  }
}
.back {
  font-size: 12px;
  a {
    text-decoration: none;
    color: #0080ff;
  }
  .router-link-active {
    text-decoration: none;
  }
}
